@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Roboto+Condensed:wght@300;400;700&display=swap);
:root {
    --primary-dark: #5651e5;
    --primary-light: #709dff;
    --dark-dark: #f8f8f8b6;
    --secundary-white: rgba(255, 255, 255, 0.685);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: "Poppins", sans-serif;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

h1 {
    font-size: 3.5rem;
}

h2 {
    font-size: 2rem;
}

.container {
    max-width: 1240px;
    margin: auto;
    padding: 0 1rem;
}

button {
    background-image: linear-gradient(45deg, #5651e5 0%, #709dff 100%);
    background-image: linear-gradient(45deg, var(--primary-dark) 0%, var(--primary-light) 100%);
    border: 1px solid #5651e5;
    border: 1px solid var(--primary-dark);
    color: #fff;
    padding: .7rem 1.5rem;
    border-radius: 8px;
    cursor: pointer;
    font-family: "Poppins", sans-serif;
}

.btn-register .btn-login {
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
}

@media screen and (max-width: 940px) {
    h1 {
        font-size: 2.5rem;
    }
    h2 {
        font-size: 1.5rem;
    }
}
.navbar {
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    z-index: 10;
    position: fixed;
    color: rgb(31, 0, 117);
}

.navbar-bg {
    background-color: rgba(0, 0, 0, 0.562);
    transition: .5s;
}

.dark {
    color: #000;
}

.logo,
.link-homee,
.link-gallery,
.link-info {
    color: rgba(1, 30, 94, 0.979);
}

.link-homee:hover {
    color: rgb(0, 0, 0);
}

.link-gallery:hover {
    color: rgb(0, 0, 0);
}

.link-info:hover {
    color: rgb(0, 0, 0);
}

.link-gallery:visited {
    color: #000000;
    text-decoration: none;
}

.navbar-bg {
    background-color: #ffffff;
}

.nav-menu {
    display: flex;
}

li {
    padding: 0 3rem;
}

.hamburger {
    display: none;
    /*padding: 1rem;*/
}

.mobile-menu {
    display: none;
}

.icon {
    font-size: 1.5rem;
    cursor: pointer;
}

@media screen and (max-width: 940px) {
    .mobile-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        top: -100vh;
        left: 0;
        position: absolute;
        justify-content: space-between;
        /*align-items: center;/*
        /*color: rgba(255,255,255, .95)*/
        background-color: #f5f5f5;
        transition: 0.3s;
        color: #000;
    }
    .active {
        top: 80px;
    }
    .mobile-menu li {
        padding: 1.2rem 1rem;
        margin: 0 1rem;
        border-bottom: 1px solid #ccc;
    }
    .mobile-nav {
        margin: 1rem 0;
    }
    .mobile-menu-botton {
        width: 100%;
        text-align: center;
        padding: 1rem 0;
    }
    .social-icons {
        display: flex;
        justify-content: space-around;
        margin: 2rem 0;
    }
    .social-icons .icon:hover {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        transition: 0.3s;
    }
    .mobile-menu button {
        margin: 1rem;
        width: 90%;
    }
    .nav-menu {
        display: none;
    }
    .nav-icons {
        display: none;
    }
    .hamburger {
        display: block;
    }
}
.hero {
    width: 100%;
    height: 100vh;
    color: #fff;
    position: relative;
}

.overlay {
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

#video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.hero .content {
    position: absolute;
    top: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    padding: 1rem;
}

.form {
    display: flex;
    background-color: #ffffff77;
    color: #000;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid #fff;
    padding: 5px;
    border-radius: 8px;
    max-width: 700px;
    width: 250px;
    margin: 0 auto;
}

@media screen and (max-width: 940px) {
    .form {
        max-width: 275px;
        text-align: column;
        justify-content: space-evenly;
    }
}
.gallery {
    width: 100%;
    text-align: center;
    padding: 4rem 0;
}

.gallery h1 {
    padding: 1rem;
}

.img-container {
    --gap: 1rem;
    --num-cols: 5;
    --row-height: 300px;
    display: grid;
    grid-template-columns: repeat(var(--num-cols), 1fr);
    margin: auto;
    padding: 1rem 0;
    grid-gap: var(--gap);
    gap: var(--gap);
}

.gallery img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.span-3 {
    grid-column: span 3;
}

.image-grid-row-2 {
    grid-row: span 2;
}

@media screen and (max-width: 940px) {
    .img-container {
        --num-cols: 2;
        --row-height: 200px;
        --grap: .5rem
    }
    .span-3 {
        grid-column: span 2;
    }
}
.footer {
    width: 100%;
    padding: 2rem 0;
    background-color: #333;
    color: #fff;
}

.footer h3 {
    color: var(--primary-withe)
}

.footer .top,
.bottom {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.footer .social {
    display: flex;
    justify-content: space-between;
    width: 280px;
}

.footer .icon {
    color: var(--primary-light);
    display: flex;
    justify-content: space-around;
    margin: 2rem 0;
}

.footer .left,
.right {
    display: flex;
    align-items: center;
}

.footer ul {
    display: flex;
}

.footer li {
    padding: .5rem;
}

@media screen and (max-width: 1140px) {
    .footer ul {
        flex-direction: column;
    }
}
