}

.content-listt {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.623);
    top: 31vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    margin: 1 auto;
    padding: 1rem;
    grid-template-columns: repeat(1, minmax(200px, 2fr));
}

.card-dog-info {
    background-color: rgba(5, 133, 172, 0.486);
    text-align: center;
    position: relative;
    max-width: 300px;
    margin: 0 auto;
    border-radius: 1rem;
    padding: 0.5rem;
    margin-bottom: 16px;
    box-shadow: 0 8px 8px rgb(0, 0, 0);
    transition: .5s;
}

.card-dog-info img {
    grid-column-start: 2;
    width: 150px;
    height: 150px;
    object-fit: cover;
    background: linear-gradient(45deg, #d47800, #e90000) border-box;
    border: 5px solid transparent;
    border-radius: 50%;
    margin: 5px;
}

.right,
.left {
    display: flex;
    justify-content: center;
}

.card-dog-info ul {
    display: relative;
}

.card-dog-info li {
    padding: .2rem;
}

